/*color*/
/*fonts*/
*, *:after, *:before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, form, fieldset, input, textarea, p, blockquote, th, td {
  padding: 0;
  margin: 0;
  outline: 0; }

button, button:active, button:focus {
  outline: none; }

a {
  text-decoration: none;
  font-size: inherit;
  color: inherit; }

table {
  border-spacing: 0; }

fieldset, img {
  border: 0; }

address, caption, cite, code, dfn, em, strong, th, var {
  font-weight: normal;
  font-style: normal; }

strong {
  font-weight: bold; }

ol, ul {
  list-style: none;
  margin: 0;
  padding: 0; }

caption, th {
  text-align: left; }

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  font-size: 100%;
  margin: 0;
  padding: 0;
  color: #000; }

q:before, q:after {
  content: ''; }

abbr, acronym {
  border: 0; }

.clear {
  display: table;
  content: '';
  clear: both; }

