  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between; }

.content_title {
  text-align: center;
  text-shadow: 0 1px 1px rgba(255, 255, 255, 0.75);
  color: #608b30;
  font-family: "Bebas Neue";
  font-size: 3rem;
  font-weight: 400;
  text-transform: uppercase; }

.call {
  color: #ffffff;
  max-width: 13.125rem;
  width: 100%;
  -webkit-box-shadow: 0 2px 5px rgba(1, 1, 1, 0.5), inset 0 1px 1px rgba(255, 255, 255, 0.29);
  box-shadow: 0 2px 5px rgba(1, 1, 1, 0.5), inset 0 1px 1px rgba(255, 255, 255, 0.29);
  background-color: #de3635;
  background-image: -webkit-gradient(linear, left bottom, left top, from(rgba(176, 39, 39, 0.54)), to(rgba(222, 54, 53, 0.54)));
  background-image: linear-gradient(to top, rgba(176, 39, 39, 0.54) 0%, rgba(222, 54, 53, 0.54) 100%);
  text-align: center;
  padding: 0.625rem 0; }
  .call a {
    width: 100%;
    max-width: 10.625rem;
    font-size: 2.3125rem;
    font-weight: 700;
